      :row-style="{height:'50px'}" :cell-style="{padding:0}" @current-change="handleSelectionChange"
      @sort-change="handleSortChange" @header-dragend="headerDragend"
      style="width: 100%;margin-bottom: 10px;min-height: 400px;" row-key="id" default-expand-all
      :tree-props="{children: 'son_project', hasChildren: 'hasChildren'}">
      <!-- <el-table-column width="50"></el-table-column> -->

      <el-table-column type="index" width="38" align="center" label="序号" :fixed="checkFixed"></el-table-column>

      <el-table-column :width="btnMaxWidth" label="合同名称" align="center" :fixed="checkFixed">
        <template slot-scope="scope">
          <!--<span>{{scope.row.contractName}}</span>-->

          <el-tooltip class="item" effect="dark" :content="scope.row.contractName" placement="right">
            <el-button :class="{pname:isPname}" :style="{maxWidth: btnMaxWidth-30 + 'px'}" type="text">
              <router-link
                :to="{name:'/ContractInformation', params:{businessNumber: scope.row.businessNumber, contractName: scope.row.contractName,contractNumber:scope.row.contractNumber,contractMoney:scope.row.contractMoney}}">
                <span style="color: #409eff;text-decoration: underline;">{{scope.row.contractName}}</span>
              </router-link>
            </el-button>
          </el-tooltip>

        </template>
      </el-table-column>
      <!-- <el-table-column width="38" label="进度" align="center">
        <template slot-scope="scope">
          <el-tooltip class="item" effect="dark" :content="scope.row.contractProgress|formatTip" placement="right">
            <div class="progress progress-red" v-if="scope.row.color==1"></div>
            <div class="progress progress-yellow" v-if="scope.row.color==2"></div>
            <div class="progress progress-green" v-if="scope.row.color==3"></div>
            <div class="progress" v-if="scope.row.color==null"></div>
          </el-tooltip>
        </template>
      </el-table-column> -->
      <!-- <el-table-column width="38" label="关注" align="center">
        <template slot-scope="scope">
          <i class="el-icon-star-on startGrey"
            :class="{'el-icon-star-on':isCared?isCared:scope.row.isCare,'startYellow':isCared?isCared:scope.row.isCare}"></i>
        </template>
      </el-table-column> -->

      <el-table-column :prop="val.key" header-align="center" :label="val.label" v-for="(val,index) in listConfig"
        :key="index" width="140"
        :align="val.key=='contractMoney'||val.key=='accumulatedCollectionMoney'||val.key=='totalBudget'||val.key=='estimatedGrossProfit'||val.key=='estimatedProfitAndLoss'||val.key=='currentGrossProfit'?'right':'center'"
        v-if="val.isShow" :sortable="val.sortable?val.sortable:null" :filters="val.filters?val.filters:null"
        :filter-method="val.filters?filterHandler:null ">
        <template slot-scope="scope">
          <span
            v-if="val.isShow&&val.isQfw==1">{{ (scope.row[val.key]==null||scope.row[val.key]=="")?'--': scope.row[val.key]| formatThousands}}</span>
          <span
            v-if="val.isShow&&val.isPercent">{{ (scope.row[val.key]==null||scope.row[val.key]=="")?'--': scope.row[val.key]*100+'%'}}</span>
          <span
            v-if="val.isShow&&val.isQfw!=1&&!val.isPercent">{{ (scope.row[val.key]==null||scope.row[val.key]=="")?'--': scope.row[val.key]}}</span>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="高级筛选" v-dialogDrag :visible.sync="advancedDialog" width="51%" center>
      <div class="dialog-wrap">
        <div class="wrap-left">
          <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="200px" class="demo-dynamic">
            <el-form-item v-for="(domain, index) in dynamicValidateForm.domains" :key="domain.key"
