                </el-option>
              </el-select>
              <el-select v-model="domain.value1" :disabled="domain.isDisabled" filterable multiple placeholder="请选择">
                <el-option v-for="(item, index) in advancedList" :key="index" :label="item.label" size="mini"
                  :value="item.label"></el-option>
              </el-select>
              <el-button type="text" @click.prevent="
                  removeDomain(domain, dynamicValidateForm.domains.length)
                " class="btn-item">
                <i class="el-icon-remove btn-del"></i>
              </el-button>
              <el-button type="text" @click.prevent="addDomain" class="btn-item">
                <i class="el-icon-circle-plus btn-plus"></i>
              </el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="advancedDialog = false" size="mini">取消</el-button>
        <el-button type="primary" @click="advancedConfirm" size="mini">确 定</el-button>
      </span>
    </el-dialog>

    <div class="page right">
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="1"
        :page-sizes="[20]" layout="total, sizes, prev, pager, next, jumper" :total="page.total"></el-pagination>
    </div>
  </div>
</template>

<script>
import {
  projectMsgById,
  summary,
  updateTime,
  care,
  contractListTabel,
  tooken,
  oaStatus,
  auditRecord,
  saveAuditRecord,
  projectListSelect,
  advancedCondition
} from "../../api/api.js";
import config from "../../config";
import { mapState, mapActions } from "vuex";
import Bus from "../../bus";

export default {
  name: "ProjectList",
  props: ["widget", "models", "rules", "remote", "datas"],
  data() {
    return {
      isStart: false,
      //是否是待登记
      checkedRegister: false,
      isPname: true,
      btnMaxWidth: 180,
      form: {
        dicName: "All",
        keyWord: ""
      },
      advancedDialog: false,
      advancedForm: {
        fieldName: "",
        fieldValue: ""
      },
      dynamicValidateForm: {
        domains: [
          {
            value: "",
            value1: "",
            isDisabled: false
          }
        ]
      },
      //是否是文本框
      isTest: false,
      checkFixed: true,
      currentPage: 1,
      advancedList: [],
      tableData: [],
      //下拉列表
      selectAry: [],
      page: {
        //当前页
        pageNum: 1,
        //每页显示条数
        pageSize: 20,
        total: 0
      },
      listConfig: [
        { key: "contractNumber", label: "合同编号（全名）", isShow: false },
        { key: "contractNumberShort", label: "合同编号", isShow: true },
        { key: "whetherItIsAnEngineeringProject", label: "是否是工程项目", isShow: true },
        { key: "contractNative", label: "合同性质", isShow: true },
        { key: "endUser", label: "客户名称", isShow: true },
        { key: "saleLevelTwoName", label: "销售B级单位", isShow: true },
        { key: "saleLevelThreeId", label: "销售C级单位", isShow: false },
        { key: "contractSaleName", label: "销售", isShow: true },
        { key: "implementLevelOneName", label: "实施B级单位", isShow: true },
        { key: "implementLevelTwoName", label: "实施C级单位", isShow: false },
        { key: "projectManagerName", label: "项目经理", isShow: true },
        { key: "contractMoney", label: "合同额", isShow: true },
        { key: "accumulatedCollectionMoney", label: "回款金额", isShow: true },
        { key: "contractProgress", label: "合同进度", isShow: true },
        { key: "beRelated", label: "关联状态", isShow: true },
        { key: "projectNumber", label: "项目编号", isShow: true },
        { key: "totalBudget", label: "采购预算", isShow: true },
        { key: "totalCost", label: "费用预算", isShow: true },
        { key: "estimatedGrossProfit", label: "预计毛利润", isShow: false },
        { key: "estimatedGrossMargin", label: "预计毛利率", isShow: false },
        { key: "estimatedProfitAndLoss", label: "预计损益", isShow: false },
        {
          key: "estimatedProfitAndLossRate",
          label: "预计损益率",
          isShow: false
        },
        { key: "currentGrossProfit", label: "当前毛利润", isShow: false },
        { key: "currentGrossMargin", label: "当前毛利率", isShow: false },
        { key: "aa", label: "当前损益", isShow: false },
        { key: "aa", label: "当前损益率", isShow: false },
        { key: "contractStatus", label: "合同状态", isShow: true },
        { key: "archivingStatus", label: "归档状态", isShow: true },
        { key: "aa", label: "所属业务群", isShow: true },
        { key: "industry", label: "行业", isShow: false },
        { key: "region", label: "区域", isShow: false },
        { key: "aa", label: "签约方", isShow: true },
        { key: "aa", label: "我方签约单位", isShow: false },
        { key: "endUser", label: "最终用户", isShow: false },
        { key: "contractSigningTime", label: "合同签订日期", isShow: false },
        { key: "aa", label: "文本签订日期", isShow: false },
        { key: "aa", label: "项目预计开始时间", isShow: false },
        { key: "aa", label: "项目预计结束时间", isShow: false }
        // { key: "contractCurrentStage", label: "合同当前阶段", isShow: true },
        // {
        //   key: "contractCurrentMilestone",
        //   label: "合同当前里程碑",
        //   isShow: true
        // },
      ],
      multipleSelection: [],
      advancedOptions: config.advancedOptions,
      advancedMultiples: {},
      dialogVisible: false,
      dialogShowField: false,
      checkedField: [],
      aLLField: [],
      tableLoading: true,
      examineDialog: false,
      passForm: {
        attitude: "1",
        comment: ""
      },
      proParams: {},
      tableData1: [
        {
          role: "公司PMO专员意见",
          content: [
            {
              msgText: "项目登记信息规范完整",
              model: "2",
              disabled: true
            },
            {
              msgText: "同意项目登记",
              model: "1"
            }
          ],
          user: { date: "2010-2-3", userName: "张三" },
          auditComment: ""
        },
        {
          role: "客户中心销售人员意见",
          content: [
            {
              msgText: "登记信息已知晓，同意项目登记",
              model: "1"
            }
          ],
          user: { date: "2010-2-3", userName: "张三" },
          auditComment: ""
        },
        {
          role: "工程中心部门经理意见",
          content: [
            {
              msgText: "项目基本信息、项目简介可通过",
              model: "1"
            },
            {
              msgText: "同意项目登记",
              model: "1"
            }
          ],
          user: { date: "2010-2-3", userName: "张三" },
          auditComment: ""
        },
        {
          role: "工程中心项目总监意见",
          content: [
            {
              msgText: "项目基本信息、项目简介可通过",
              model: "1"
            },
            {
              msgText: "同意项目登记",
              model: "1"
            }
          ],
          user: { date: "2010-2-3", userName: "张三" },
          auditComment: ""
        },
        {
          role: "工程中心总经理意见",
          content: [
            {
              msgText: "登记信息已知晓，同意项目登记",
              model: "1"
            }
          ],
          user: { date: "2010-2-3", userName: "张三" },
          auditComment: ""
        }
      ],
      //项目列表筛选字段名称
      fieldName: "",
      //项目列表字段筛选内容
      fieldContent: [],
      //项目列表排序字段名称
      sortFieldName: "",
      //项目列表排序 1 升序 2，降序
      sort: null,
      isCared: null,
      tookenId: "",

      //审核进度
      auditConfig: {
        10: "登记待提交",
        11: "登记审核中",
        12: "登记已审核",
        20: "启动待提交",
        21: "启动审核中",
        22: "启动已审核",
        30: "工作结项待提交",
        31: "工作结项审核中",
        32: "工作结项已审核"
      }
    };
  },
  filters: {
    formatThousands: function(value) {
      if (value == "--") {
        return "--";
      }
      value = value / 10000;
      value = value.toFixed(2);
      var r = /^[0-9]*[1-9][0-9]*$/; //正整数
      if (r.test(value) == false) {
        return (
          value &&
          value.toString().replace(/(\d)(?=(\d{3})+\.)/g, function($0, $1) {
            return $1 + ",";
          })
        );
      } else {
        var reg = /\d{1,3}(?=(\d{3})+$)/g;
        return (value + "").replace(reg, "$&,");
      }
    },
    formatTip: function(value) {
      var num = value != "" ? parseFloat((value * 100).toFixed(10)) : "--";
      if (num < 10) {
        return `进度正常(${num}%)`;
      } else if (num >= 10 && num < 30) {
        return `一般延期(${num}%)`;
      } else if (num >= 30) {
        return `严重延期(${num}%)`;
      } else {
        return "--";
      }
      // if (value == null) {
      //   return "--";
      // } else if (value == "0.0") {
      //   return "正常";
      // } else {
      //   return parseFloat((value * 100).toFixed(10)) + "%";
      // }
    }
  },
  watch: {
    datas: {
      deep: true,
      handler: function(val) {
        console.log(this.datas);
        console.log(this.datas.options.url_a);
      }
    }
  },
  computed: {
    ...mapState([
      "userinfo", //os跳转过来的用户信息值
      "cacheContractParams", //合同列表查询条件缓存条件
      "gjFromContractModel"
    ]),

    projectType() {
      return this.datas.options.pro_type;
    },
    copyListConfig() {
      return JSON.parse(JSON.stringify(this.listConfig));
    },

    listCode() {
      return this.form.dicName;
    },
    asideHeight: function() {
      return document.documentElement.clientHeight - 205;
    },
    isShowBtn(){
      if(this.userinfo.account_id=="8416498716893399290"||this.userinfo.account_id=="-1814768306395265967"){
        return false
      }else{
        return true
      }
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    ...mapActions([
      "setDetailPage",
      "setCacheContractParams",
      "setGJFromContractModel"
    ]),
    contractRegistration() {
      this.$router.push({
        path: "contractproject",
        query: {
          contractNumber: this.multipleSelection.contractNumber,
          type: "contract"
        }
      });
    },
    init() {
      this.getContractTable();
      //下拉选框
      this.getSelectList();
    },
    headerDragend(newWidth, oldWidth, column, event) {
      this.btnMaxWidth = newWidth;
      if (newWidth > 200) {
        this.isPname = false;
      } else {
        this.isPname = true;
        this.btnMaxWidth = 200;
      }
    },
    registerChange(val) {
      let params = {
        code: this.listCode,
        pageNum: this.page.pageNum,
        pageSize: this.page.pageSize,
        memberId: this.userinfo.memberid,
        keyWord:
          this.form.keyWord != undefined
            ? this.form.keyWord.trim()
            : this.form.keyWord,
        companyId: this.userinfo.account_id,
        whetherToRegister: val == false ? 0 : 1
      };
      this.getContractTable(params);
      this.setCacheContractParams(params);
    },
    advancedConfirm() {
      this.setCacheContractParams({});
      let array = this.dynamicValidateForm.domains;
      let obj = {};
      array.forEach(item => {
        if (item.value1.length > 0) {
          obj[item.value] = item.value1;
        }
      });
      this.advancedMultiples = obj;
      let params = {
        code: this.listCode,
        pageNum: this.page.pageNum,
        pageSize: this.page.pageSize,
        memberId: this.userinfo.memberid,
        companyId: this.userinfo.account_id,
        fieldName: this.fieldName,
        content: this.fieldContent,
        multiples: this.advancedMultiples,
        whetherToRegister: this.checkedRegister == false ? 0 : 1
      };
      this.getContractTable(params);
      this.setGJFromContractModel(this.dynamicValidateForm);
      this.advancedDialog = false;
    },
    advancedChange(val, index) {
      this.dynamicValidateForm.domains[index].value1 = "";
      // if (obj == "projectManager") {
      //   this.isTest = true;
      // } else {
      //   this.isTest = false;
      // }

      advancedCondition(
        `/sgpm/rest/api/server/pm/project/condition?projectKey=${val}`,
        null
      )
        .then(res => {
          this.advancedList = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    popoverShow() {
      this.checkFixed = false;
      // this.asideHeight = document.documentElement.clientHeight - 260;
    },
    popoverHide() {
      this.checkFixed = true;
      // this.asideHeight = document.documentElement.clientHeight - 260;
    },
    advancedDialogOpen() {
      this.advancedDialog = true;
      this.dynamicValidateForm = this.gjFromContractModel;
    },
    //获取项目列表表格
    getContractTable(val) {
      this.tableLoading = true;
      var params = this.cacheContractParams;
      if (val) {
        params = val;
      } else {
        if (JSON.stringify(params) == "{}") {
          params = {
            code: "All",
            pageNum: 1,
            pageSize: 20,
            whetherToRegister: this.checkedRegister == false ? 0 : 1
          };
        }
        params = Object.assign(params, {
          memberId: this.userinfo.memberid,
          companyId: this.userinfo.account_id
        });
        this.form.keyWord = params.keyWord;
        this.form.dicName = params.code;
        this.checkedRegister = params.whetherToRegister == 1 ? true : false;
      }

      //获取合同列表信息
      contractListTabel("/sgpm/rest/api/integration/contracts", params)
        .then(res => {
          this.tableLoading = false;
          let result = res.data.data || [];
          this.tableData = res.data.data || [];
          this.page.total = res.data.meta.totalCount;
          this.setCacheContractParams(params);
        })
        .catch(err => {
          console.log(err);
        });
    },
    keyWordSearch() {
      let params = {
        code: this.listCode,
        pageNum: this.page.pageNum,
        pageSize: this.page.pageSize,
        memberId: this.userinfo.memberid,
        keyWord: this.form.keyWord.trim(),
        companyId: this.userinfo.account_id,
        whetherToRegister: this.checkedRegister == false ? 0 : 1
      };
      this.getContractTable(params);
    },
    //获取查看范围下拉选框
    getSelectList() {
      //获取下拉选信息
      projectListSelect("/sgpm/rest/api/server/pm/project/item?param=1", null)
        .then(res => {
          var result = res.data.data;
          this.selectAry = result;
        })
        .catch(err => {
          console.log(err);
        });
    },
    selectChange(val) {
      let params = {
        code: this.listCode,
        memberId: this.userinfo.memberid,
        pageNum: this.page.pageNum,
        pageSize: this.page.pageSize,
        memberId: this.userinfo.memberid,
        companyId: this.userinfo.account_id,
        whetherToRegister: this.checkedRegister == false ? 0 : 1
      };
      this.getContractTable(params);
    },
    handleCurrentChange(val) {
      let params = {
        code: this.listCode,
        memberId: this.userinfo.memberid,
        pageNum: val,
        pageSize: this.page.pageSize,
        memberId: this.userinfo.memberid,
        companyId: this.userinfo.account_id,
        whetherToRegister: this.checkedRegister == false ? 0 : 1
      };
      this.getContractTable(params);
    },
    filterHandler(value, row, column) {
      if (column.filteredValue.length != 0) {
        this.fieldName = column.property;
        this.fieldContent = column.filteredValue;

        let params = {
          code: this.listCode,
          memberId: this.userinfo.memberid,
          pageNum: this.page.pageNum,
          pageSize: this.page.pageSize,
          memberId: this.userinfo.memberid,
          fieldName: column.property,
          content: column.filteredValue,
          multiples: this.advancedMultiples,
          whetherToRegister: this.checkedRegister == false ? 0 : 1
        };
        this.getContractTable(params);
        this.$refs.refTable.clearFilter();
      }
    },
    clearFilter() {
      this.$refs.refTable.clearFilter();
      this.form.keyWord = "";
      this.form.dicName = "All";
      this.checkedRegister = false;
      this.advancedMultiples = {};
      this.setGJFromContractModel({
        domains: [{ value: "", value1: [], isDisabled: false }]
      });
      let params = {
        code: "All",
        pageNum: 1,
        pageSize: 20,
        companyId: this.userinfo.account_id,
        memberId: this.userinfo.memberid,
        multiples: {},
        whetherToRegister: 0
      };
      this.getContractTable(params);
    },
    handleSortChange(column, prop, order) {
      this.sortFieldName = column.prop;
      this.sort = column.order == "ascending" ? 1 : 2;
      let params = {
        code: this.listCode,
        memberId: this.userinfo.memberid,
        pageNum: this.page.pageNum,
        pageSize: this.page.pageSize,
        memberId: this.userinfo.memberid,
        sortFieldName: column.prop,
        sort: column.order == "ascending" ? 1 : 2
      };
      this.getContractTable(params);
    },
    handleSelectionChange(val) {
      console.log(val);
      localStorage.setItem("projectInfo", JSON.stringify(val));

      if (val) {
        //如果是登记已审核可以启动
        this.isStart = true;
      }
      this.multipleSelection = val;
    },
    handleCommandAdd(val) {
      localStorage.removeItem("projectMsg");
      this.setDetailPage(false);
      sessionStorage.setItem("isRevoke", 2);
      localStorage.removeItem("keyWords");
      localStorage.removeItem("keytechnologys");
      if (val == "a") {
        localStorage.removeItem("businessProjectId");
        localStorage.removeItem("globalParams");
        this.$router.push({
          path: "Businessproject",
          query: {
            judgelogo: val
          }
        });
      } else if (val == "b") {
        localStorage.removeItem("businessProjectId");
        localStorage.removeItem("globalParams");
        this.$router.push({
          path: "Contractproject",
          query: {
            judgelogo: val
          }
        });
      } else if (val == "c") {
        localStorage.removeItem("globalParams");
        localStorage.removeItem("keyWords");
        localStorage.removeItem("keytechnologys");
        localStorage.setItem("proId", this.multipleSelection.id);
        this.$router.push({
          name: "/projectstart",
          params: {
            id: this.multipleSelection.id,
            category: this.multipleSelection.jumpType,
            judgestart: 99
          }
        });
      } else if (val == "d") {
        this.$router.push({
          path: "nonContractual",
          query: {
            id: this.multipleSelection.id,
            judgelogo: val,
            projectNum: this.multipleSelection.projectNum
          }
        });
      }
    },
    //启动项目
    startPorject(e) {
      localStorage.removeItem("globalParams");
      localStorage.removeItem("keyWords");
      localStorage.removeItem("keytechnologys");
      localStorage.setItem("proId", this.multipleSelection.id);
      this.$router.push({
        name: "/projectstart",
        params: {
          id: this.multipleSelection.id,
          category: this.multipleSelection.jumpType,
          judgestart: 99
        }
      });
    },
    handleCell(row, column, cell, event) {
      if (cell.cellIndex === 3) {
        //d_care_id 如果有就说明是已关注的项目进行取消，如果没有就是添加关注
        let param = {
          id: row.d_care_id,
          user_id: this.userinfo.memberid,
          project_id: row.id
        };
        if (row.d_care_id) {
          this.$message({
            message: "取消关注",
            type: "success"
          });
          cell.firstChild.firstChild.className = "el-icon-star-on startGrey";
        } else {
          this.$message({
            message: "关注成功",
            type: "success"
          });
          cell.firstChild.firstChild.className = "el-icon-star-on startYellow";
        }
        care("/sgpm/rest/api/server/pm/project/care", param)
          .then(res => {
            if (res.data.code == 200) {
              this.getContractTable();
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    removeDomain(item, size) {
      if (size == 1) {
        this.$notify({
          title: "警告",
          message: "最后一条啦",
          type: "warning"
        });
        return;
      }
      var index = this.dynamicValidateForm.domains.indexOf(item);
      if (index !== -1) {
        this.dynamicValidateForm.domains.splice(index, 1);
      }
    },
    addDomain() {
      this.dynamicValidateForm.domains.push({
        value: ""
      });
      this.dynamicValidateForm.domains.forEach((item, index) => {
        if (this.dynamicValidateForm.domains.length - 1 == index) {
          item.isDisabled = false;
        } else {
          item.isDisabled = true;
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.el-form--label-top .el-form-item__label {
  float: left;
}
.el-form-item__label {
  width: 100% !important;
}
.el-table-filter__checkbox-group label.el-checkbox {
  margin: 0 0 0 0;
}

.list-container {
  padding: 0 15px 10px 15px;
  .el-form-item--mini.el-form-item,
  .el-form-item--small.el-form-item {
    margin-bottom: 0px;
  }
  .header {
    .circle-btn {
      border-radius: 5px;
    }
    .one-row {
      margin: 10px 0;
    }
    .header-search {
      float: right;
    }
  }
  .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }
  .startGrey {
    color: #c4c4c4;
    font-size: 13px;
    display: block;
  }
  .startYellow {
    color: #ff9900;
    font-size: 13px;
  }
  .search {
    margin: 30px 0 0 0;
  }
  .progress {
    display: inline-block;
    width: 10px;
    height: 10px;
    background: gray;
    border-radius: 50%;
    position: relative;
  }
  .progress-red {
    background: #f56954 !important;
  }
  .progress-yellow {
    background: #f39c12 !important;
  }
  .progress-green {
    background: #00a65a !important;
  }
  .el-table__expanded-cell {
    padding: 0;
  }
  .el-form-item__label {
    width: 70px !important;
  }

  .list-header {
    padding-bottom: 10px;
    border-bottom: 1px solid #e0e0e0;
    .label {
      font-size: 12px;
      vertical-align: -webkit-baseline-middle;
    }
    .btn-item {
      display: flex;
      justify-content: flex-end;
      margin: 5px 0 0 0;
      .circle-menu {
        border-radius: 2px;
      }
      .btn-right {
        margin: 0 10px 0 0;
      }
      .btn-left {
        margin: 0 0 0 10px;
      }
    }

    .tittle {
      font-weight: bold;
      font-size: 16px !important;
    }
    .title-position {
      text-align: left;
    }
    .btn-drop {
      margin: 0 10px 0 0;
    }
    .popover {
      height: 200px;
    }
  }

  .right {
    float: right;
  }

  .father-pro-table {
    // overflow-y: auto;
    .el-table--scrollable-x .el-table__body-wrapper {
      overflow-x: hidden;
    }
    .num {
      color: #409eff;
      text-decoration: underline;
    }
  }
  .dialog {
    .red {
      color: red;
    }
  }
  .demo-dynamic {
    position: relative;
    .btn-del,
    .btn-plus {
      font-size: 18px;
      color: #409eff;
      top: 2px;
    }
  }
  .pname {
    // max-width: 100px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
  .dialog-wrap {
    display: flex;
    .wrap-left {
      width: 100%;
      display: flex;
      justify-content: center;
      border-right: 1px solid #dbdbdb;
      .el-form-item__content {
        line-height: 2px;
      }
    }
    .wrap-right {
      width: 25%;
      display: flex;
      justify-content: center;
    }
    .bg-purple {
      width: 116px;
    }
    .btn-item {
      margin: 0 0 0 4px;
    }
  }
  .search-form {
    display: flex;
    padding: 20px 0 10px 0;
    .search-left {
      width: 30%;
    }
    .search-right {
      width: 70%;
      text-align: right;
    }
  }
}
</style>



